html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

@import url("https://fonts.googleapis.com/css2?family=Mada&family=Six+Caps&family=Tenor+Sans&display=swap");

:root {
  font-family: "Mada", sans-serif;
  box-sizing: border-box;
}

button {
  font-family: "Mada", sans-serif;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: #ef0e1e;
  color: white;
  padding: 20px;
  border: none;
  border-radius: 20px;
  box-shadow: 2px 2px 10px -2px #000000;
  transition: transform 0.5s, box-shadow 0.5s;
}

button:hover {
  cursor: pointer;
  transform: translateX(3px) translateY(3px);
  box-shadow: 5px 5px 15px -3px #000000;
}
